                stepped: true,
                normir: true,
            }
            break;
        case EnumDiagName.:
            sett = {
                type: EnumDiagType.Bar,
                diagName: EnumDiagName.,
            }
            break;
        case EnumDiagName.:
            sett = {
                type: EnumDiagType.Bar,
