}

interface IOption {
    id: number;
    //accum?: boolean;
    name: string
    childs?: IChildOption[]
    active?: boolean
    icon?: string
}

interface IChildOption {
    id: number
    accum: boolean //yfr
    name: string
    img: string
    active: boolean
    icon: string

}
function deactivateAll(options: IOption[]) {
    options.forEach(option => {
        option.active = false;
        if (option.childs) {
            deactivateAll(option.childs);
        }
    });
}
</script>